import {Tweens} from "core/ui/animations/tweens"
import {BiPosXY} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {ImageWithBackground} from "core/ui/controls/imageWithBackground"
import {Undef} from "core/utils/undef"
import {Images, Sounds} from "games/detective/assets"

const biPositions = Sizes.bi({
  next: {x: 0, y: 300},
  current: {x: 0, y: 600},
  prev: {x: 0, y: 900},
}, {
  next: {x: 300, y: 0},
  current: {x: 600, y: 0},
  prev: {x: 900, y: 0},
})

const durations = {
  animation: 300
}

const scale = 0.7
const alpha = 0.2

export class CarouselComponent extends GameComponent<[number, number | undefined]> implements OnInit {

  nextImg: ImageWithBackground

  currentImg: ImageWithBackground

  prevImg: ImageWithBackground

  prev2Img: ImageWithBackground

  onInit(): void {
    this.prev2Img = this.createImg(biPositions.prev).updateFields({visible: false})
    this.prevImg = this.createImg(biPositions.prev).updateFields({visible: false})
    this.nextImg = this.createImg(biPositions.next)
    this.currentImg = this.createImg(biPositions.current, true)
  }

  createImg(pos: BiPosXY, isCurrent: boolean = false) {
    return this.add(new ImageWithBackground(this.gameKey(Images.card), this.gameKey(Images.item(0))))
      .setBiPos(pos)
      .anchorCenter().fluentUpdate(x => {
        x.symmetricScale = isCurrent ? 1 : scale
        x.frontImg.alpha = isCurrent ? 1 : alpha
      })
  }

  animation() {
    this.playSound(Sounds.moving_cards)
    const nextPos = this.current(biPositions.next)
    const currentPos = this.current(biPositions.current)
    this.currentImg.setPos(nextPos)
    this.currentImg.symmetricScale = scale
    this.currentImg.frontImg.alpha = alpha
    this.bringToTop(this.prevImg)

    this.disableInput(true)
    Tweens.start({
      target: this.prevImg.frontImg,
      reverse: true,
      to: {alpha: 1},
      duration: durations.animation
    })
    Tweens.onPercent(
      Tweens.start({
        target: this.prevImg,
        reverse: true,
        to: {...currentPos, symmetricScale: 1},
        easing: Phaser.Easing.Sinusoidal.In,
        duration: durations.animation,
      }), 30, () => {
        this.bringToTop(this.currentImg)
        Tweens.start({
          target: this.currentImg,
          to: {...currentPos, symmetricScale: 1},
          easing: Phaser.Easing.Sinusoidal.Out,
          duration: durations.animation,
          onComplete: () => {
            if (!this.first) {
              this.prev2Img.visible = true
            }
            this.disableInput(false)
          }
        })

        Tweens.start({
          target: this.currentImg.frontImg,
          to: {alpha: 1},
          duration: durations.animation
        })

      }
    )
  }

  first = true

  onPropsUpdate() {

    if (!this.first) {
      this.prevImg.visible = true
    }
    this.prev2Img.frontKey = this.prevImg.frontKey
    this.prevImg.frontKey = this.currentImg.frontKey
    if (this.first) {
      this.currentImg.frontKey = this.gameKey(Images.item(this.props[0]))
    } else {
      this.currentImg.frontKey = this.nextImg.frontKey
    }
    const nextItem = this.props[1]
    if (Undef.is(nextItem)) {
      this.nextImg.visible = false
    } else {
      this.nextImg.frontKey = this.gameKey(Images.item(nextItem))
    }
    if (!this.first) {
      this.animation()
    }
    this.first = false
  }
}



