import {translationService} from "core/services/instances"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {BiValue} from "core/ui/common/positions"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {TextPanelComponent} from "core/ui/components/games/textPanelComponent"
import {TutorialTextComponent} from "core/ui/components/games/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {Control} from "core/ui/controls/index"
import {screenSizeService} from "core/ui/services/instances"
import {Undef} from "core/utils/undef"
import {Colors, Fonts, Images, Sounds, Translations} from "games/detective/assets"
import {Task} from '../../rememberLogic'
import {AnswersComponent} from "./answersComponent"

const positions = Sizes.create({
  item: {y: 260},
  panel: {width: 680, height: 150},
  tutorial: {y: 840},
  tutorialPanel: {width: (1100 as number | undefined)},
  mainGroup: {y: 0},
  maxWidth: 660
})

const positionsPortrait = Sizes.update(positions, {
  item: {y: 100},
  tutorialPanel: {width: undefined},
  mainGroup: {y: -80},
  tutorial: {y: 1020},
})

const biValues = BiValue.create({
  bgVisible: true,
  panelColor: (Colors.panel as number | undefined)
}, {
  bgVisible: false,
  panelColor: undefined
})

const durations = {
  lightDelay: 300
}

const getPositions = () => screenSizeService.isPortrait ? positionsPortrait : positions

export class MainComponent extends GameComponent<Task> implements OnInit {
  bg = this.addImage([Images.bg, [0, 0]])
  bg1 = this.addImage([Images.bg1(0), [0, 0]]).onOrientationChange(biValues.bgVisible, (c, v) => c.visible = v)
  mainGroup = this.add(new Control())
  item: TextPanelComponent

  answers = this.mainGroup.add(this.createComponent(AnswersComponent))
  private tutorial: TutorialTextComponent

  onInit(): void {
    this.item = this.mainGroup.add(new TextPanelComponent({
      size: positions.panel,
      font: Fonts.wordText,
      color: Colors.panel
    }))
      .anchorCenterX().updatePos(Sizes.w2, getPositions().item.y)

    if (this.isTutorial) {
      this.tutorial = this.add(this.createComponent(TutorialTextComponent).make({
          marginTop: getPositions().tutorial.y,
          textAlign: "center",
          panel: {color: this.current(biValues.panelColor), size: getPositions().tutorialPanel}}))

      this.tutorial.setProps(translationService.getTranslation(Translations.subtitle))
      this.mainGroup.setPos(getPositions().mainGroup)

    }
  }

  index = 0

  onAnswer(answer: ILastAnswer) {
    this.index++
    this.updateTask()
    if (AnswerStatus.isMain(answer.status)) {
      this.answers.showFinalScene(this.props)
      this.item.visible = false
    }

    if (this.isTutorial && answer.status == AnswerStatus.Correct) {
      this.tutorial.visible = false
      this.item.visible = false
    }
  }

  updateTask() {
    if (this.index >= this.props.data.length) {
      return
    }
    const task = this.props.data[this.index]
    this.item.setProps(task.words[0])
    this.answers.setProps(task.itemIds)
  }

  lightOn(){
    this.item.visible = false
    this.answers.visible = false
    this.bg1.key = this.gameKey(Images.bg1(0))
    Undef.map(this.tutorial, x => x.visible = false)
    this.delay(durations.lightDelay).addOnce(() => {
      this.playSound(Sounds.lamp_on)
      this.bg1.key = this.gameKey(Images.bg1(1))
      this.item.visible = true
      this.answers.visible = true
      Undef.map(this.tutorial, x => x.visible = true)
    })
  }

  onPropsUpdate() {
    // this.answers.showFinalScene(this.props)
    this.index = 0
    this.updateTask()
    if(!screenSizeService.isPortrait){
      this.lightOn()
    }
  }

}



