import {Cnst, Locale} from "core/logic/cnst"
import {settingService} from "core/services/instances"
import ru from "./wordsRu"

const wordsDB: Partial<{[lang in Locale]: { 1: string[], 2: string[], 3: string[] }}> = {
  ru
}
export module Words {
  export type WordDifficulty = "easy" | "normal" | "hard"

  export function getWords(): {[key in WordDifficulty]: string[]} {
    const localeWords = wordsDB[settingService.settings.locale] || wordsDB[Cnst.defaultLocale]!
    return {
      easy: localeWords[1],
      normal: localeWords[1].concat(localeWords[2]),
      hard: localeWords[1].concat(localeWords[2], localeWords[3])
    }
  }

}




