import {CoreColors, CoreFonts} from "core/assets/index"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {GraphicsImg} from "core/ui/controls/GraphicsImg"
import {ImageWithBackground} from "core/ui/controls/imageWithBackground"
import {Btn, Control} from "core/ui/controls/index"
import {screenSizeService} from "core/ui/services/instances"
import {RandomGenerator} from "core/utils/index"
import {Colors, Images, Sounds} from "games/detective/assets"
import {Task} from "games/detective/rememberLogic"
import * as _ from "lodash"

const positions = Sizes.create({
  margin: {x: 340, y: 320, absolute: true},
  maxWidth: 1200,
  nitStart: [300, -80],
  cardMinOffset: {x: 0, y: 50},
  lineWidth: 20,
  lineMargin: 15,
  curve: 80,
  answerY: 750,
  shift: 30,
})

const maxFinalSceneImageCount = 6

const positionsPortrait = Sizes.update(positions, {
  maxWidth: 700,
  answerY: 760,
})

const durations = {
  animation: 500,
  angle: {move: 300, fall: 800}
}

const getPositions = () => screenSizeService.isPortrait ? positionsPortrait : positions

export class AnswersComponent extends GameComponent<number[]> implements OnInit {

  images: Btn[] = []

  group = this.add(new Control()).anchorCenter().updatePos(Sizes.w2, getPositions().answerY)

  onInit(): void {
    this.onChangeForRun(() => screenSizeService.isPortrait, x => {
      this.updatePositions()
    })
  }

  rng = new RandomGenerator()

  colCount = () => screenSizeService.isPortrait ? 2 : 3
  rowCount = () => screenSizeService.isPortrait ? 3 : 2

  showFinalScene(task: Task) {
    this.group.removeAll(true)
    const images = _.take(task.answers.seq, maxFinalSceneImageCount).map(x => this.createImage(x as number).anchorCenterX())
    const colCount = this.colCount()
    this.group.addChildren(images)
    Sizes.tableFromList(images, colCount, {...getPositions().margin, x: getPositions().maxWidth / colCount})
    images.forEach(x => {
      ControlUtl.randomPositionShift(this.rng, x, 0, 10)
      // x.visible = false
    })
    this.group.updatePivot()
    const imagePositions = images.map(img => [img.x, img.y])
    this.drawNits(imagePositions)

    this.group.alpha = 0
    Tweens.start({
      target: this.group,
      to: {alpha: 1},
      duration: durations.animation
    })

  }

  drawNit(graphics: Phaser.Graphics, [xs, ys]: number[], [x, y]: number[]) {
    graphics.moveTo(xs, ys)
    const to = [x + positions.cardMinOffset.x, y + positions.cardMinOffset.y]
    graphics.bezierCurveTo(xs, ys, xs * 1.3, ys * 0.3, to[0], to[1])
    return to
  }

  drawNits(pos: number[][]) {
    let knopkaPositoin: (number)[][] = []
    const nits = this.group.add(new GraphicsImg(g => {
      g.lineStyle(positions.lineWidth, Colors.nit)
      knopkaPositoin = pos.map(x => this.drawNit(g, positions.nitStart, x))
    }))
    knopkaPositoin.forEach(to => this.group.add(this.makeImage([Images.knopka, to]).anchorCenter()))
    nits.updatePos(pos[0][0] - positions.lineMargin, positions.nitStart[1] - positions.lineMargin)
    this.group.add(this.makeImage([Images.knopka, positions.nitStart]).anchorCenter())
  }

  onAnswer(answer: ILastAnswer) {
    if (AnswerStatus.isMain(answer.status)) {
    }
  }

  updatePositions() {
    Sizes.tableFromList(this.images, this.colCount(), getPositions().margin)
  }

  createImage(item: number) {
    return new ImageWithBackground(this.gameKey(Images.card_mini), this.gameKey(Images.itemSmall(item)))
      .fluentUpdate(x => {
        x.updatePosition()
      })
  }

  onPropsUpdate() {
    this.group.removeAll(true)
    this.images = this.props.map(item => {
        return this.addAnswerBehavior(new Btn("",
          () => {
          }, CoreFonts.button(CoreColors.black),
          this.createImage(item)
        ).anchorCenterX(), item, {location: "top", dark: true})
          .onHover((out, btn) => {
            if (out) {
              CommonAnimations.angleElasticZero(btn, durations.angle.fall)
            } else {
              CommonAnimations.toAngle(btn, this.rng.sign() * 5, durations.angle.move)
              this.playSound(Sounds.cards)
            }
          })
      }
    )
    this.group.addChildren(this.images)
    this.updatePositions()
    this.group.updatePivot()
    this.answersAreReady()
  }
}




