import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {IntroAnimationComponent} from "core/ui/components/games/introAnimationComponent"
import {Behavior} from "core/ui/controls/behaviors/behavior"

export class IntroComplexAnimationBehavior extends Behavior<BaseMainGameComponent> {
  private tween: Phaser.Tween
  private intro: IntroAnimationComponent
  onComplete: Phaser.Signal

  constructor() {
    super()
  }

  onInit() {
    const duration = IntroAnimationComponent.durations.delay
    this.tween = this.control.prepareDefaultIntroAnimation(duration)
    this.intro = this.control.add(this.control.createComponent(IntroAnimationComponent))
      .anchorCenter().updatePos(Sizes.w2, Sizes.h2)
    this.intro.x -= this.control.cameraOffset.x
    this.onComplete = this.tween.onComplete
  }

  start() {
    this.intro.startAnimation()
    this.tween.start()
  }
}




