import {RandomGenerator} from "core/utils/randomGenerator"
import * as _ from "lodash"

export class CircleSeq {
  current = 0

  constructor(private max: number) {
  }

  next() {
    const v = this.current
    this.current = (this.current + 1) % this.max
    return v
  }
}

export class UniqSeq {
  private index = 0
  private list: number[]

  constructor(public max: number, private rng = new RandomGenerator()) {
    this.updateList()
  }

  checkMax(newMax: number) {
    if (newMax != this.max) {
      this.max = newMax
      this.updateList()
    }
  }

  private updateList() {
    let newList = this.rng.uniqArray(this.max, this.max)
    if (this.list) {
      const prev = _.last(this.list)
      if (prev == newList[0]) {
        newList = _.tail(newList)
        newList.push(prev)
      }
    }
    this.list = newList
  }

  private updateIndex() {
    this.index += 1
    if (this.index >= this.list.length) {
      this.updateList()
      this.index = 0
    }
  }

  nextArray(count: number) {
    return _.range(count).map(() => this.next())
  }

  next() {
    const v = this.list[this.index]
    this.updateIndex()
    return v
  }

  nextFrom<T>(arr: T[]) {
    this.checkMax(arr.length)
    return arr[this.next()]
  }

  nextSubsetFrom<T>(arr: T[], count: number) {
    return _.range(count).map(() => this.nextFrom(arr))
  }
}



