import {TextStyle} from "core/assets/fonts"
import {Sizes} from "core/ui/common/sizes"
import {OnInit} from "core/ui/components/onInit"
import {Behavior} from "core/ui/controls/behaviors/behavior"
import {Control} from "core/ui/controls/control"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {Txt} from "core/ui/controls/txt"

const positions = Sizes.create({
  margin: 30,
})

export class TextOnCenterBehavior extends Behavior<Control> implements OnInit {

  text: Txt
  maxWidth = 0

  constructor(private font: TextStyle, private margin = positions.margin) {
    super()
  }

  onInit() {
    this.text = ControlUtl.addOnCenter(this.control, new Txt("    ", this.font))
    this.text.textControl.wordWrap = true
  }

  refreshSize() {
    this.text.visible = false
    this.maxWidth = this.control.width - 2 * this.margin
    this.text.textControl.wordWrapWidth = this.maxWidth * 1.5
    this.text.setPos(this.control.center())
    this.text.visible = true
    ControlUtl.scaleToFit(this.text, this.maxWidth)
    this.text.updatePivot()

  }

  setText(txt: string) {
    this.text.text = txt
    this.refreshSize()
  }

}



