import {IGameMetaData} from "core/logic"
import {LevelOptions, ScoreOptions} from "core/types"
import {MainComponent} from "games/detective/components/mainComponent"
import {Logic} from "games/detective/logic"
import {Images, Sounds} from "./assets"
import {levels} from "./levels"

export const gameId = "detective"
const scoreOptions: ScoreOptions = {
  scoreAnswer: 100,
  scoreBonus: 0,
  scoreBonusPerLevel: 5
}
const levelOptions: LevelOptions = {
  levels,
  levelsSeqCounts: [{seqCount: 1, maxLevel: 100}]
}

export const metaData: IGameMetaData = {
  gameId,
  title: "Detective",
  logic: Logic,
  component: MainComponent,
  levelOptions,
  scoreOptions,
  ignoreError: true,
  darkBg: true,
  tutorialTaskCount: "manual",
  images: Images.data,
  sounds: Sounds.keys,
  atlasCount: 7,
  size: {
    width: 2048,
    height: 1280,
    heightMin: 1152
  }
}



