import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {OnInit} from "core/ui/components/onInit"
import {IntroComplexAnimationBehavior} from "core/ui/controls/behaviors/introComplexAnimationBehavior"
import {Sounds} from "games/detective/assets"
import {Task} from "games/detective/logic"
import * as Main from "./main/mainComponent"
import * as Remeber from "./remember/mainComponent"

export class MainComponent extends BaseMainGameComponent implements OnInit {
  main? = this.add(this.createComponent(Main.MainComponent))
  rememeber?: Remeber.MainComponent

  onInit(): void {
    this.startIntro()
    this.context.uiService.mainComponent.onIntroComplete.addOnce(() => {
      this.playSound(Sounds.bg, true)
    })
  }

  introAnimation() {
    const behavior = new IntroComplexAnimationBehavior()
    this.addBehavior(behavior)
    return behavior
  }

  onNewTask(task: Task) {
    this.context.uiService.answerStatus.hide = task.isMain
    if (task.isMain) {
      if (!this.main) {
        const signal = this.context.uiService.answerStatus.onAnimationComplete
        this.disableInputOn(signal).addOnce(() => {
          this.main = this.add(this.createComponent(Main.MainComponent))
          this.main.setProps(task)
          if (this.rememeber) {
            this.rememeber.destroy(true)
            this.rememeber = undefined
          }
        })
      } else {
        this.main.setProps(task)
      }
    } else {
      if (!this.rememeber) {
        this.playSound(Sounds.level_up)
        this.rememeber = this.add(this.createComponent(Remeber.MainComponent))
        if (this.main) {
          this.main.destroy(true)
          this.main = undefined
        }
      }
      this.rememeber.setProps(task)
    }
  }

}



