import * as F from "core/assets/fonts"
import {Sys} from "core/logic/sys"
import {ImagesMetaData} from "core/types"
import {Sizes} from "core/ui/common/sizes"
import {itemCount} from "games/detective/mainLogic"
import * as _ from "lodash"

export module Fonts {

  export const wordText = {
    fontSize: 70 * Sys.pixelRatio, font: F.CoreFonts.MontserratSemiBold, fill: F.CoreColors.white
  }
}


export module Sounds {

  export const click_letter = "click_letter"
  export const folder = "folder"
  export const hover_letter = "hover_letter"
  export const moving_cards = "moving_cards"
  export const bg = "bg"
  export const cards = "cards"
  export const lamp_on = "lamp_on"
  export const level_up = "level_up"

  export const keys = [
    click_letter,
    folder,
    hover_letter,
    moving_cards,
    bg,
    cards,
    lamp_on,
    level_up,
  ]

}

export module Colors {

  export const nit = 0xDE3918
  export const panel = 0x281203
}

export module Images {

  export const sizes = Sizes.create({
    item: {h: 480, w: 480},
  })

  export const bg = "bg"
  export const bg1 = (i: number)  => "bg1_" + i
  export const card_mini = "card_mini"
  export const card = "card"
  export const folder = "folder"
  export const paper = "paper"
  export const polosk = "polosk"
  export const knopka = "knopka"

  export function item(id: number) {
    return "item_" + id
  }

  export function nit(id: number) {
    return "nit_" + id
  }

  export function itemSmall(id: number) {
    return "item_small_" + id
  }

  const itemList = _.range(itemCount).map(item)

  export const keys = [
    bg,
    "bg_left",
    "bg_intro",
    card_mini,
    card,
    folder,
    paper,
    polosk
  ].concat(itemList)
  export const data: ImagesMetaData = {
    sprites: keys
  }

}

export module Translations {
  export const title = "title"
  export const subtitle = "subtitle"
}




