import {Behavior} from "core/ui/controls/behaviors/behavior"
import {Btn} from "core/ui/controls/btn"

export class BtnAlphaHover extends Behavior<Btn> {
  constructor() {
    super()
  }

  onInit() {
    const alpha = 0.5
    this.control.alpha = alpha
    this.control.onHover(out => {
      this.control.alpha = out ? alpha : 1
    })
  }
}




