import {ImageKey} from "core/types/index"
import {Img} from "core/ui/controls/index"

export class ImageWithBackground extends Img {
  frontImg: Img

  constructor(back: ImageKey, key: ImageKey) {
    super(back)
    this.frontImg = this.add(new Img(key))
    this.updatePosition()
  }

  get frontKey() {
    return this.frontImg.key
  }

  set frontKey(v) {
    this.frontImg.key = v
  }

  updatePosition() {
    this.frontImg.img.anchor.set(0.5, 0.5)
    this.frontImg.position.set(this.img.width / 2, this.img.height / 2)
  }
}



