import {CoreFonts} from "core/assets"
import {TextStyle} from "core/assets/fonts"
import {Sizes} from "core/ui/common/sizes"
import {Size} from "core/ui/common/types"
import {Component} from "core/ui/components/component"
import {PanelComponent, PanelProps} from "core/ui/components/games/panelComponent"
import {TextOnCenterBehavior} from "core/ui/controls/behaviors/textOnCenterBehavior"

const positions = Sizes.create({
  marginTop: 30,
  textMarginX: 40,
  padding: 60
})

export class TextPanelComponent extends Component<string> {
  private textBehavior: TextOnCenterBehavior
  background: PanelComponent

  constructor(props: { size: Size, color?: number, font?: TextStyle }) {
    super()

    this.background = this.add(new PanelComponent())

    this.background.setProps(props)
    this.textBehavior = new TextOnCenterBehavior({...(props.font || CoreFonts.tutorial(false)), wordWrap: false})
    this.background.addBehavior(this.textBehavior)
  }

  changePanel(props: PanelProps) {
    this.background.setProps(props)
    this.textBehavior.refreshSize()
    this.updatePivot()
  }

  onPropsUpdate(): void {
    this.textBehavior.setText(this.props)

  }

}



