import {SoundKey} from "core/types/assets"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {Behavior} from "core/ui/controls/behaviors/behavior"
import {Btn} from "core/ui/controls/btn"

export class BtnHoverSoundBehavior extends Behavior<Btn> {
  constructor(private gameCompoent: GameComponent, private key: SoundKey) {
    super()
  }

  onInit() {
    this.control.onHover(out => {
      if(!out){
        this.gameCompoent.playSound(this.key)
      }
    })
  }
}




