import {createLevelsFromArray} from "core/types/logic"
import {Words} from "games/detective/words"
import WordDifficulty = Words.WordDifficulty

export interface Level {
  taskCount: number
  wordCount: number
  memorizeCount: number
  answerCount: number
  difficulty: WordDifficulty,
}

function level(difficulty: WordDifficulty, taskCount: number, wordCount: number, answerCount: number, memorizeCount = 1): Level {
  return {difficulty, taskCount, wordCount, answerCount, memorizeCount}
}

export const levels = createLevelsFromArray([
  level("easy", 3, 5, 2),
  level("easy", 3, 5, 3),
  level("easy", 4, 5, 3),
  level("easy", 5, 5, 3),
  level("easy", 5, 4, 4),
  level("normal", 6, 4, 4),
  level("normal", 7, 3, 4),
  level("normal", 8, 3, 5),
  level("normal", 9, 2, 5),
  level("normal", 10, 2, 6),
  level("normal", 10, 1, 6),
  level("normal", 6, 5, 6, 2),
  level("normal", 5, 5, 6, 3),
  level("hard", 8, 2, 6, 2),
  level("hard", 6, 3, 6, 3),
])




