import {GameLogic, ITask} from "core/logic/index"
import {UniqSeq} from "core/utils/circleSeq"
import {RandomGenerator} from "core/utils/index"
import {Undef} from "core/utils/undef"
import {Words} from "games/detective/words"
import {Level} from "./levels"

export const itemCount = 71
export type Word = string

export interface Task extends ITask {
  words: Word[]
  itemId: number
  nextItemId?: number
  isMain: true
}

export class Logic {
  words = Words.getWords()

  rng = new RandomGenerator()
  uniqItem = new UniqSeq(itemCount, this.rng)
  uniqWord = new UniqSeq(this.words.easy.length, this.rng)
  prevTask: Task
  index = 0

  nextTask(level: Level) {
    if (this.index == level.taskCount) {
      return undefined
    }
    const isLast = this.index == level.taskCount - 1
    const allWords = this.words[level.difficulty]
    const words = this.uniqWord.nextSubsetFrom(allWords, level.wordCount)
    const itemId = Undef.map(this.prevTask, x => x.nextItemId) || this.uniqItem.next()
    const nextItemId = isLast ? undefined : this.uniqItem.next()
    const task: Task = {
      words,
      itemId,
      nextItemId,
      isMain: true,
      nonScore: true,
      answers: {count: level.memorizeCount}
    }
    this.prevTask = task
    this.index++
    return task
  }

}





