import {translationService} from "core/services/instances"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {TutorialTextComponent} from "core/ui/components/games/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {Control} from "core/ui/controls/control"
import {screenSizeService} from "core/ui/services/instances"
import {Images, Sounds, Translations} from "games/detective/assets"
import {CarouselComponent} from "games/detective/components/main/carouselComponent"
import {Task} from '../../mainLogic'
import {AnswersComponent} from "./answersComponent"

const allPositions = Sizes.bi({
  item: {x: 627, y: 40},
  answer: {x: 1350, y: 170},
  mainGroup: {y: 120}
}, {
  item: {x: 665, y: 290},
  answer: {x: 1023, y: 480},
  mainGroup: {y: 180},
})

const duratoins = {
  delay: 1800
}

export class MainComponent extends GameComponent<Task> implements OnInit {
  bg = this.addImage([Images.bg, [0, 0]])
  mainGroup = this.add(new Control())
  item = this.mainGroup.add(this.createComponent(CarouselComponent))
    .setBiPos(allPositions.item)

  answers = this.mainGroup.add(this.createComponent(AnswersComponent)).anchorCenterX()
    .setBiPos(allPositions.answer)
  private tutorial: TutorialTextComponent

  onInit(): void {
    if (screenSizeService.isPortrait) {
      this.answers.symmetricScale = 0.9
      this.item.symmetricScale = 0.6
    }
    if (this.isTutorial) {

      this.tutorial = this.add(this.createComponent(TutorialTextComponent).make(
        {textAlign: "center"}))
      this.tutorial.setProps(translationService.getTranslation(Translations.title))
      this.mainGroup.setBiPos(allPositions.mainGroup)
    }
    if (screenSizeService.isPortrait) {
      if (this.isFirstTask) {
        if (this.tutorial) {
          this.tutorial.visible = false
        }
        this.item.visible = false
        this.delay(this.isFirstTask ? duratoins.delay : 0).addOnce(() => {
          if (this.tutorial) {
            CommonAnimations.setVisible(this.tutorial, true)
          }
          CommonAnimations.setVisible(this.item, true)
        })
      }
    }

  }

  onPropsUpdate() {
    const task = this.props
    this.item.setProps([task.itemId, task.nextItemId])
    this.answers.setProps(task.words)
    if (this.isFirstTask) {
      this.answers.visible = false
      this.context.uiService.mainComponent.onIntroComplete.addOnce(() => {
        this.answers.visible = true
        this.playSound(Sounds.folder)
        this.disableInputOn(CommonAnimations.appearFromBottom(this.answers).onComplete)
      })

    }
  }

}



