import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"

const positions = Sizes.create({
  wordMargin: {x: 20, y: 0}
})

const durations = {
  animation: 1000,
  delay: 700
}

export class IntroAnimationComponent extends GameComponent {
  static durations = durations

  onInit() {
    this.addImage("bg_intro")
  }

  startAnimation(notStart = false) {
    return Tweens.start({
      target: this,
      to: {symmetricScale: 3.5, alpha: 0},
      delay: durations.delay,
      duration: durations.animation,
      notStart,
      easing: Phaser.Easing.Sinusoidal.InOut,
      onComplete: () => this.destroy(true)
    })

  }
}




