import {CoreColors, CoreFonts} from "core/assets/index"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {OnInit} from "core/ui/components/onInit"
import {BtnAlphaHover} from "core/ui/controls/behaviors/btnAlphaHoverBehavior"
import {BtnHoverSoundBehavior} from "core/ui/controls/behaviors/btnHoverSoundBehavior"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {Btn, Control} from "core/ui/controls/index"
import {screenSizeService} from "core/ui/services/instances"
import {Images, Sounds} from "games/detective/assets"
import {Word} from "games/detective/mainLogic"

const positions = Sizes.create({
  margin: {x: 50, y: 20},
  group: {x: 140, y: 270},
  maxWidth: 600
})

const positionsPortrait = Sizes.expand(positions, {
  margin: {x: 0, y: 0}
})

const getPositions = () => screenSizeService.isPortrait ? positionsPortrait : positions

export class AnswersComponent extends GameComponent<Word[]> implements OnInit {
  folder = this.addImage([Images.folder, [0, 0]])

  btns: Btn[] = []
  group = this.add(new Control()).setPos(positions.group)

  onInit(): void {
    this.onChangeForRun(() => screenSizeService.isPortrait, x => {
      this.updatePositions()
    })
  }

  onAnswer(answer: ILastAnswer) {
    if (answer.status == AnswerStatus.CorrectPartial) {
      this.btns.find(x => x.tag == answer.answerId)!.visible = false
    }
  }

  updatePositions() {
    Sizes.tableColumn(this.btns, getPositions().margin)
  }

  onPropsUpdate() {
    this.group.removeAll(true)
    this.btns = this.props.map(word => {
        const btn = this.addAnswerBehavior(new Btn(word, () => {
        }, CoreFonts.button(CoreColors.lightBlack, 60))
          .updateFields({tag: word})
          .addBehavior(new BtnAlphaHover())
          .addBehavior(new BtnHoverSoundBehavior(this, Sounds.hover_letter))
          , word, {location: "right"})
        return ControlUtl.scaleToFit(btn, positions.maxWidth)
      }
    )
    this.group.addChildren(this.btns)
    this.updatePositions()
    this.group.updatePivot()
  }
}




