import {GameLogic, ITask} from "core/logic/index"
import {AnswerId} from "core/types/logic"
import {Arrays} from "core/utils/arrays"
import {RandomGenerator} from "core/utils/index"
import {tuple} from "core/utils/tuples"
import {Level} from "games/detective/levels"
import * as Main from "games/detective/mainLogic"
import * as _ from "lodash"

export type Word = string

export interface Task extends ITask {
  data: {
    words: Word[]
    itemIds: number[]
    reverse: boolean
  }[]
  isMain: false
  answers: { seq: (number | Word)[] }
}

export class RememberLogic {
  setTaskAnswers(taskAnswers: [ITask, AnswerId[]][]): void {
    this.allTasks = this.rng.derangement(_.flatMap(taskAnswers, ([task, answers]) => (answers as string[])
      .map(answer => tuple((task as Main.Task).itemId, answer))))
    const [items, words] = Arrays.unzip(this.allTasks)
    this.allWords = _.uniq(words)
    this.allItems = _.uniq(items)
  }

  rng = new RandomGenerator()
  allTasks: [number, string][]
  allWords: Word[]
  allItems: number[]

  getAnswers<T>(prev: T, allAnswers: T[], level: Level) {
    const otherAnswers = this.rng.subset(_.difference(allAnswers, [prev]), level.answerCount - 1)
    return this.rng.shuffle([prev], otherAnswers)
  }

  createAnswer(prevItem: number, prevAnswer: string, level: Level) {
    const reverse = true
    const itemIds = reverse
      ? this.getAnswers(prevItem, this.allItems, level)
      : [prevItem]
    const words = reverse ? [prevAnswer] : this.getAnswers(prevAnswer, this.allWords, level)
    const answer = reverse ? prevItem : prevAnswer
    const answerTask = {
      words,
      itemIds,
      reverse,
      answer
    }
    return answerTask

  }

  nextTask(level: Level) {
    const data = this.allTasks.map(x => this.createAnswer(x[0], x[1], level))
    const answers = {seq: data.map(x => x.answer)}
    const task: Task = {
      isMain: false,
      data,
      answers
    }
    return task
  }

}





